.showImg{
  width: 100%;
  padding-top: 15px;
}
.showImg-active{
  width: 100%;
  vertical-align: middle;
  object-fit: cover;
  object-position: center;
}
.listImg{
  padding:0;
  margin:0;
  list-style-type: none;
  transition: margin-left linear 0.2s;
}

.imgHover{
  display:block;
  border:2px solid #fff;
  padding: 3px;
  margin: 0;
  overflow: hidden;
  width:90px;
  height:90px;
  box-sizing: border-box;
  float:left;
  background-color: var(--white-color);
}
.listImg--active,.imgHover:hover{
  border-color: #00483d;
}
.imgHover img{
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.listImg{
  overflow: hidden;
  overflow-x: scroll;
  height: 90px;
}
.listImg::-webkit-scrollbar{
  display: none;
}
.slide-wrap{
  width: 450px;
  overflow: hidden;
  height: 90px;
  position: relative;
}
#controls label{
  transition: opacity 0.2s ease-out;
  display: none;
  width: 20px;
  height: 20px;
  position: relative;
}
#controls label::before{
  content: '';
  position: absolute;
  width: 25px;
  height: 40px;
  background-color:rgba(0, 0, 0, 0.3);
  top:50%;
  transform: translateY(-50%);
  border-radius: 2px;
}
#slide11:checked ~ #slides{
  margin-left: 0px;
}
#slide22:checked ~ #slides{
  margin-left: -90px;
}
#slide33:checked ~ #slides{
  margin-left: -180px;
}
#slide44:checked ~ #slides{
  margin-left: -270px;
}
#slide11:checked ~ #controls label:nth-child(2),
#slide22:checked ~ #controls label:nth-child(3),
#slide33:checked ~ #controls label:nth-child(4),
#slide44:checked ~ #controls label:nth-child(1) {
